<template>
	<div class="header">
		<van-tabs color="#ccc" class="header-top">
			<van-tab title="首页" to="/home">
				<div class="van-swiper">
					<van-swiper-text></van-swiper-text>
					<div v-for="(v,k) in msg" :key='v._id'>

						<van-card :price="v.price" :desc="v.dataName" :title="v.dataTitle" :thumb="v.dataImg"
							:origin-price="v.original" @click='show1(v)' />
					</div>
				</div>
				<slot></slot>
			</van-tab>
			<van-tab title="用户">
				<div v-for="(v,k) in msg" :key='v._id'>

					<van-cell-group>
						<van-cell :title='v.dataName' :label="v.dataTitle" :value='v.original' @click='cart(v)'/>
					</van-cell-group>

				</div>
				<van-sku v-model="show" :sku="sku" :goods="goods"  :hide-stock="sku.hide_stock" :message-config="messageConfig"
					 />

			</van-tab>
			<van-tab title="关注">
				<div v-for="(v,k) in msg" :key='v._id'>

					<van-cell-group>
						<van-cell :title='v.dataName' :label="v.dataTitle"
							:to='`/detail/${v._id}?collectionName=banner`' />
					</van-cell-group>
				</div>

			</van-tab>
		</van-tabs>
	</div>
</template>

<script>
	import VanSwiperText from '../components/van-swiper/van-swiper-text.vue'
	import Vue from 'vue';

	import {
		Loading
	} from 'vant';

	import {
		Cell,
		CellGroup
	} from 'vant';

	Vue.use(Cell);
	Vue.use(CellGroup);


	export default {
		name: 'van-header',
		data() {
			return {
				msg: [],
				show: false,
				sku: {},
				goods: {},
				messageConfig: {},
			}
		},
		components: {
			VanSwiperText,
		},
		mounted() {

			this.$axios({
				url: '/news/banner',
				params: {
					_limit: 10
				}
			}).then(res => {
				console.log(this.$route);
				this.msg = res.data.data

			})
		},
		methods: {
			show1(v) {
				console.log(v);
				this.$router.push('/detail/' + v._id + '?collectionName=banner')
			},
			cart(v){
				this.show=true,
				this.sku = {
				  // 所有sku规格类目与其值的从属关系，比如商品有颜色和尺码两大类规格，颜色下面又有红色和蓝色两个规格值。
				  // 可以理解为一个商品可以有多个规格类目，一个规格类目下可以有多个规格值。
				  tree: [
				    {
				      k: '颜色', // skuKeyName：规格类目名称
				      k_s: 's1', // skuKeyStr：sku 组合列表（下方 list）中当前类目对应的 key 值，value 值会是从属于当前类目的一个规格值 id
				      v: [
				        {
				          id: '1', // skuValueId：规格值 id
				          name: v.dataName, // skuValueName：规格值名称
				          imgUrl: v.dataImg, // 规格类目图片，只有第一个规格类目可以定义图片
				          previewImgUrl: v.dataImg, // 用于预览显示的规格类目图片
				        }
				      ],
				      largeImageMode: true, //  是否展示大图模式
				    }
				  ],
				  // 所有 sku 的组合列表，比如红色、M 码为一个 sku 组合，红色、S 码为另一个组合
				  list: [
				    {
				      id: 2259, // skuId
				      s1: '1', // 规格类目 k_s 为 s1 的对应规格值 id
				      s2: '1', // 规格类目 k_s 为 s2 的对应规格值 id
				      price: v.price*100, // 价格（单位分）
				      stock_num: 110 // 当前 sku 组合对应的库存
				    }
				  ],
				  price: v.price, // 默认价格（单位元）
				  stock_num: 227, // 商品总库存
				  collection_id: 2261, // 无规格商品 skuId 取 collection_id，否则取所选 sku 组合对应的 id
				  none_sku: false, // 是否无规格商品
				  messages: [
				    {
				      // 商品留言
				      datetime: '0', // 留言类型为 time 时，是否含日期。'1' 表示包含
				      multiple: '0', // 留言类型为 text 时，是否多行文本。'1' 表示多行
				      name: '备注', // 留言名称
				      type: 'text', // 留言类型，可选: id_no（身份证）, text, tel, date, time, email
				      required: '1', // 是否必填 '1' 表示必填
				      placeholder: '填写备注' // 可选值，占位文本
				    }
				  ],
				  hide_stock: false // 是否隐藏剩余库存
				},
				this.goods= {
				  // 默认商品 sku 缩略图
				  picture: v.dataImg
				},
				this.customStepperConfig={
				  // 自定义限购文案
				  quotaText: '每次限购xxx件',
				  // 自定义步进器超过限制时的回调
				  handleOverLimit: (data) => {
				    const { action, limitType, quota, quotaUsed, startSaleNum } = data;
				
				    if (action === 'minus') {
				      Toast(startSaleNum > 1  ? `${startSaleNum}件起售` : '至少选择一件商品');
				    } else if (action === 'plus') {
				      // const { LIMIT_TYPE } = Sku.skuConstants;
				      if (limitType === LIMIT_TYPE.QUOTA_LIMIT) {
				        let msg = `单次限购${quota}件`;
				        if (quotaUsed > 0) msg += `，你已购买${quotaUsed}`;
				        Toast(msg);
				      } else {
				        Toast('库存不够了');
				      }
				    }
				  },
				  // 步进器变化的回调
				  handleStepperChange: currentValue => {},
				  // 库存
				  stockNum: 1999,
				  // 格式化库存
				  stockFormatter: stockNum => {},
				}
			}
		}
	}
</script>

<style scoped>
	.header-top {}

	.jiazai {
		position: absolute;
		left: 44%;
		top: 200px;
	}
</style>
